/**
 * 同一个js文件被导入多次，他们引用的还是同一个内存对象
 * 例如Vue模块再其它文件被导入，他和此文件的Vue引用同一个对象
 */
import Vue from "vue";
import store from "./vuex/store";
import VueRouter from "vue-router";
import routes from "./routers"; // 引入路由配置
import VueLazyload from "vue-lazyload"; // 引入图片懒加载模块
import "common/css/index.styl"; // 引入公共样式
import common from "./common/js/common.js";
import sharejs from "./common/js/sharejs.js";
import "../static/js/rem.js";
import axios from "./common/js/http.js";
import { savaToLocal, loadFromlLocal } from "./common/js/storage.js"; // 公共方法：本地缓存

var firstRouter = true; //当刷新页面的时候可能会直接跳转至某个页面，设置这个参数重定向至index界面

// 注册组件
Vue.use(common);
Vue.use(VueRouter);
const router = new VueRouter({
  mode: "hash",
  linkActiveClass: "active", // 路由当前激活的状态
  routes // （缩写）相当于 routes: routes
});
axios.globalRouter = router;
Vue.prototype.$http = axios;

// error，loading是图片路径, 用require引入 attempt为加载图片的次数
Vue.use(VueLazyload, {
  error: require("./assets/404.png"),
  loading: require("./assets/loading.gif"),
  attempt: 1
});

//不是在上线环境下，正式上线webpack会自动将process.env.NODE_ENV设置为production，所以process.env.NODE_ENV不需要手动更改
if (process.env.NODE_ENV != "production") {
  // 美体师
  // savaToLocal("aml", "openid", "93f97192da2d93140830dd0dc01765be");
  // savaToLocal("aml", "userid", "871");
  // savaToLocal("aml", "token", "5060334b13ff9ef41257ae47a22f0ac1");
  //业务员
  // savaToLocal("aml", "openid", "496a00a05b1f310128abe34c35b09adf");
  // savaToLocal("aml", "userid", "868");
  // savaToLocal("aml", "token", "3ad63d54d43f91e590d5648cb3148084");
  //普通用户
  // savaToLocal("aml", "openid", "1c5a66b8c36b7c93b573d786edb86a64");
  // savaToLocal("aml", "userid", "872");
  // savaToLocal("aml", "token", "4eb34813959c3d991b40ec3448ae13c2");
  //代理商
  // savaToLocal("aml", "openid", "8f932344e931ff24bd68b8e128423513");
  // savaToLocal("aml", "userid", "869");
  // savaToLocal("aml", "token", "9697ec96c22807d3339f61a172a73a36");
  //商家
  savaToLocal("aml", "openid", "3f47df186df1890ed84c0ddff0980d76");
  savaToLocal("aml", "userid", "867");
  savaToLocal("aml", "token", "c283d7b354088516f1b654eebfc2afa4");
}

// 页面刷新时，重新赋值token
let openid = loadFromlLocal("aml", "openid", "");
let userid = loadFromlLocal("aml", "userid", "0");
let token = loadFromlLocal("aml", "token", "");

if (!openid || userid == "0") token = "";

store.commit("UPDATE_OPNEID", openid);
store.commit("UPDATE_USERID", userid);
store.commit("UPDATA_TOKEN", token);

// 路由跳转之前回掉函数
router.beforeEach((to, from, next) => {
  //当刷新页面，统一先跳动index路劲
  if (firstRouter) {
    firstRouter = false;
    if (token) {
      Vue.prototype.updatateUserInfo(() => {
        next({ path: "/home", replace: true });
      });
    } else{
      next({ path: "/home", replace: true });
    }
    return;
  }

  if (to.matched.some(r => r.meta.requireAuth && !store.state.token)) {
    next({ path: "/login", query: { redirect: to.fullPath } });
    return;
  }
  // 只有params时才给路径加上参数
  // 从store中取出付给params，此处注意路径未必完全吻合，以你的为准
  if (Object.keys(to.params).length == 0) {
    Object.assign(to.params, store.state.paramMap[to.name] || {});
  }
  store.commit("REFRESHROUTERPARAM", { key: to.name, value: to.params }); // 存储一下url对应的params备用
  store.dispatch("toggleLoading", true);
  next();
});

router.afterEach((to,from) => {
  //更换当前页面的标题
  if (to.params && to.params.titleName) {
    window.setDocumentTitle(to.params.titleName);
  } else if (to.meta.title) window.setDocumentTitle(to.meta.title);
  //改变背景颜色
  if (to.meta && to.meta.bodyBgColor) {
    document.body.style.backgroundColor = to.meta.bodyBgColor;
  }
  store.dispatch("toggleLoading", false);
});
/**
 * 创建和挂载根实例。
 * 记得要通过 router 配置参数注入路由，
 * 从而让整个应用都有路由功能
 */
const routerApp = new Vue({
  store,
  router
}).$mount("#app");

// window.setCookie("amxl_detailsid", "181");

sharejs.defultShareMsg();
sharejs.getWxData();

export default routerApp;



