<template>
  <div class="app-wapper">
    <consume-ok-popup></consume-ok-popup>
    <receive-ok-popup></receive-ok-popup>
    <!-- exclude为不缓存的组件，注意是组件名，不是路由名称 -->
    <!-- <keep-alive exclude='v-details,pay-affirm,pledge-affirm,agency-shops,userlist,user-buyed,ticket-list,refresh-page,store-buyed,income-expense,bind-name,bind-bank,bank-list,get-money,shop-oreder-home'>
      <router-view class="router-view"></router-view>
    </keep-alive> -->
    <transition mode="out-in" v-on:before-enter="beforeEnter" v-on:enter="enter" v-on:before-leave="beforeLeave" v-on:leave="leave" >
      <keep-alive>
          <router-view v-if="$route.meta.keepAlive" class="router-view"></router-view>
      </keep-alive>
    </transition>
    <transition  mode="out-in" v-on:before-enter="beforeEnter" v-on:enter="enter" v-on:before-leave="beforeLeave" v-on:leave="leave">
      <router-view v-if="!$route.meta.keepAlive" class="router-view"></router-view>
    </transition>
  </div>
</template>

<script>
import consumeOkPopup from "./components/popupBox/consumeOkPopup.vue";
import receiveOkPopup from "./components/popupBox/receiveOkPopup.vue";
import sharejs from "./common/js/sharejs.js";
import { mapState } from "vuex";
import $ from "jquery";

export default {
  name: "app",
  components: {
    consumeOkPopup,
    receiveOkPopup
  },
  data() {
    return {};
  },
  computed: {
    ...mapState(["showTitle"])
  },
  created() {},
  activated() {},
  methods: {
    beforeEnter: function(el) {
      el.style.opacity = 0;
      if (this.$route.meta && this.$route.meta.scrollReset) {
        document.body.scrollTop = 0;
      }
    },
    enter: function(el, done) {
      $(el).fadeTo(250,1,done);
    },
    beforeLeave: function(el) {

    },
    leave: function(el, done) {
      done();
    }
  }
};
</script>

<style lang="stylus">
.fade-enter-active, .fade-leave-active {
  transition: opacity 0.25s;
}

.fade-enter, .fade-leave-to { /* .fade-leave-active below version 2.1.8 */
  opacity: 0;
}

#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif, 'Microsoft YaHei';
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #2c3e50;
  width: 100%;
  height: 100%;
}

.app-wapper {
  width: 100%;
  height: 100%;

  .router-view {
    height: 100%;
    box-sizing: border-box;
  }
}
</style>


