// require.ensure 是 Webpack 的特殊语法，用来设置 组件到底路径
/**
 * 1.定义路由，每个路由应该映射一个组件
 * path : 浏览器的显示的路径
 * name ： 路由的名字
 * component : 路由的组件路径
 */
import App from './App'
const home = r => require.ensure([], () => r(require('./components/mainView/home.vue')), 'home');
const combineHome = r => require.ensure([], () => r(require('./components/mainView/combine/combineHome.vue')), 'combineHome');
const discountCombine = r => require.ensure([], () => r(require('./components/mainView/combine/discountCombine.vue')), 'discountCombine');
const shopsList = r => require.ensure([], () => r(require('./components/mainView/combine/shopsList.vue')), 'shopsList');
const me = r => require.ensure([], () => r(require('./components/mainView/me.vue')), 'me');
const webShopHome = r => require.ensure([], () => r(require('./components/mainView/webShop/webShopHome.vue')), 'webShopHome');
const webShopAll = r => require.ensure([], () => r(require('./components/mainView/webShop/webShopAll.vue')), 'webShopAll');
const webShopSpecial = r => require.ensure([], () => r(require('./components/mainView/webShop/webShopSpecial.vue')), 'webShopSpecial');
const webShopIntegral = r => require.ensure([], () => r(require('./components/mainView/webShop/webShopIntegral.vue')), 'webShopIntegral');
const webShopAdvise = r => require.ensure([], () => r(require('./components/mainView/webShop/webShopAdvise.vue')), 'webShopAdvise');
const smartStoreHome = r => require.ensure([], () => r(require('./components/megoods/smartStore/smartStoreHome.vue')), 'smartStoreHome');
const smartProjectList = r => require.ensure([], () => r(require('./components/megoods/smartStore/smartProjectList.vue')), 'smartProjectList');
const coBrands = r => require.ensure([], () => r(require('./components/megoods/smartStore/coBrands.vue')), 'coBrands');
const details = r => require.ensure([], () => r(require('./components/details/details.vue')), 'details');
const payAffirm = r => require.ensure([], () => r(require('./components/payPage/payAffirm.vue')), 'payAffirm');
const pledgeAffirm = r => require.ensure([], () => r(require('./components/payPage/pledgeAffirm.vue')), 'pledgeAffirm');
const myqcode = r => require.ensure([], () => r(require('./components/megoods/qrcode/myqcode.vue')), 'myqcode');
const bankList = r => require.ensure([], () => r(require('./components/megoods/bankList.vue')), 'bankList');
const bindBank = r => require.ensure([], () => r(require('./components/megoods/bindBank.vue')), 'bindBank');
const getMoney = r => require.ensure([], () => r(require('./components/megoods/getMoney.vue')), 'getMoney');
const userlist = r => require.ensure([], () => r(require('./components/megoods/userlist.vue')), 'userlist');
const bindname = r => require.ensure([], () => r(require('./components/megoods/bindname.vue')), 'bindname');
const ticketQcode = r => require.ensure([], () => r(require('./components/megoods/qrcode/ticketQcode.vue')), 'ticketQcode');
const integralQRcode = r => require.ensure([], () => r(require('./components/megoods/qrcode/integralQRcode.vue')), 'integralQRcode');
const refreshPage = r => require.ensure([], () => r(require('./components/popupBox/refreshPage.vue')), 'refreshPage');
const extuser = r => require.ensure([], () => r(require('./components/megoods/extuser.vue')), 'extuser');
const agencyChainHome = r => require.ensure([], () => r(require('./components/megoods/agency/under/agencyChainHome.vue')), 'agencyChainHome');
const agencyChain = r => require.ensure([], () => r(require('./components/megoods/agency/under/agencyChain.vue')), 'agencyChain');
const agencySalesman = r => require.ensure([], () => r(require('./components/megoods/agency/under/agencySalesman.vue')), 'agencySalesman');
const agencyTJT = r => require.ensure([], () => r(require('./components/megoods/agency/under/agencyTJT.vue')), 'agencyTJT');
const agencyChainSearch = r => require.ensure([], () => r(require('./components/megoods/agency/under/agencyChainSearch.vue')), 'agencyChainSearch');
