const userBuyed = r => require.ensure([], () => r(require('./components/megoods/purchaseHistory/userBuyed.vue')), 'userBuyed');
const mealList = r => require.ensure([], () => r(require('./components/megoods/purchaseHistory/mealList.vue')), 'mealList');
