<template>
    <mt-popup class="receive-popup" popup-transition="popup-fade" :modal=true v-model="isShow">
        <div class="popup-close">
            <i class="iconfont icon-close" @click="hidePopup"></i>
        </div>
        <div class="ok-icon">
            <i class="iconfont icon-chenggong"></i>
        </div>
        <div class="tips">
            <p class="receive-tip">收款成功！</p>
        </div>
        <mt-button size="small" class="sure" type="default" @click="hidePopup">确定</mt-button>
    </mt-popup>
</template>

<script>
import { mapState } from "vuex";
import Vue from "vue";
import { Popup, Button } from "mint-ui";
import "mint-ui/lib/button/style.css";
import "mint-ui/lib/popup/style.css";
Vue.component(Button.name, Button);
Vue.component(Popup.name, Popup);

export default {
  name: "receive-ok-popup",
  components: {},
  created() {},
  methods: {
    hidePopup(event) {
      if (event._constructed) return; // 去掉betterscroller自带click事件的点击
      this.changeRouterHandle();
      this.$store.commit("UPDATE_RECEIVEOKSHOW", false);
    },
    changeRouterHandle() {
      if (this.receiveOkPopupShow == false) return;
      if (this.$route.name == "myqcode") {
        this.$router.go(-1);
      }
    }
  },
  computed: {
    ...mapState(["receiveOkPopupShow"]),
    isShow: {
      get: function() {
        return this.receiveOkPopupShow;
      },
      set: function(boo) {
        if (boo == false) this.changeRouterHandle();
        this.$store.commit("UPDATE_RECEIVEOKSHOW", boo);
      }
    }
  }
};
</script>

<style lang="stylus" rel="stylesheet/stylus" scoped>
.receive-popup {
    width: 88%;
    border-radius: 8px;
    text-align: center;

    .popup-close {
        text-align: right;

        .iconfont {
            color: #acacac;
            font-size: 26px;
            line-height: 26px;
            font-weight: 700;
            margin: 8px 8px 5px 0;
            display: inline-block;
        }
    }

    .ok-icon {
        .iconfont {
            color: #42495C;
            font-size: 54px;
            line-height: 54px;
            font-weight: 500;
            display: inline-block;
        }
    }

    .tips {
        margin: 12px 0px 20px 0px;
    }

    p {
        color: #42495C;
        font-size: 17px;
        line-height: 18px;
        font-weight: 500;
        letter-spacing 1px;
    }

    .sure {
        width: 40%;
        height: 32px;
        box-shadow: none;
        margin-bottom: 25px;
        color: #fff;
        background-color: #42495C;
        opacity 0.9;
        letter-spacing 3px;
        font-size 15px;
    }
}
</style>



